body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  border-radius: 4px;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 52px;
  height: 52px;
  width: 52px;
  margin: 1px;
  padding: 0;
  text-align: center;
  cursor: pointer;
}
.square.zero {
  border-color: #ccc;
  font-size: 0px;
  background: #ccc;
  cursor: default;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}
.new-game {
  margin-left: 5px;
}
.game-board {
  border: 1px solid #999;
  border-radius: 4px;
  background: #ccc;
  margin-top: 10px;
  margin-bottom: 10px;
}
.game-status {
  color: red;
  font-weight: 700;
}
.game-info {
  margin-left: 20px;
}